import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ProductService } from 'src/app/service/product.service';
import { schema } from 'ngx-editor';
import { DOMSerializer } from 'prosemirror-model';
import { errorMessage } from 'src/app/messages';
declare var swal: any, $: any;
@Component({
  selector: 'app-product-view',
  templateUrl: './product-view.component.html'
})
export class ProductViewComponent implements OnInit {
  viewId: string;
  objectData: any;
  selectedFile: File;
  showLoader: boolean;
  productImageArray: any;
  selectedFilestatus = false;

  constructor(private activatedRoute: ActivatedRoute, private productService: ProductService) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.viewId = this.activatedRoute.snapshot.paramMap.get('id');
    }
  }

  ngOnInit(): void {
    this.productService.getProductById(this.viewId).subscribe((res) => {
      if (res.success) {
        this.objectData = res.data ? res.data : [];
        if (this.objectData.product_detail) {
          const contentNode = schema.nodeFromJSON(JSON.parse(this.objectData.product_detail));
          const html = DOMSerializer.fromSchema(schema).serializeFragment(contentNode.content);
          document.getElementById("prodetails").appendChild(html);
        }
      }
    });
    this.getProductImages();
  }
  getjson(data) {
    if (data)
      return JSON.parse(data);
  }

  fileChangeEvent(fileInput: any) {
    const reg = /(.*?)\.(jpg|jpeg|png|gif|giff)$/;
    if (!fileInput.target.files[0].name.match(reg)) {
      swal.fire(
        'Invalid File!',
        'Please select valid file ',
        'error'
      );
      this.removeFile();
      return false;
    } else {
      this.selectedFilestatus = true;
      this.selectedFile = fileInput.target.files[0];
    }
  }
  removeFile() {
    this.selectedFile = null;
    this.selectedFilestatus = false;
  }

  addImage() {
    document.getElementById("remove").click();
    $('#bottom-modal').modal('show')
  }
  submitImage(): void {
    this.showLoader = true;
    const formData = new FormData();
    formData.append('product_id', this.viewId);
    if (this.selectedFile) {
      formData.append('file', this.selectedFile);
    }

    this.productService.addProductImages(formData).subscribe((response) => {
      this.showLoader = false;
      if (response.success) {
        $('#bottom-modal').modal('hide');
        this.getProductImages();
        this.removeFile();

      } else {
        this.showLoader = false;
      }
    });
  }

  getProductImages() {
    const params: any = {};
    if (this.viewId) {
      params.product_id = this.viewId;
    }
    this.productService.getProductImageList(params).subscribe((response) => {
      if (response.success) {
        this.productImageArray = response.data.data;
      } else {
        this.productImageArray = [];
      }
    }, (error) => {
      this.productImageArray = [];
    });
  }

  deleteObject(object) {
    swal.fire({
      title: errorMessage.delete_header_text,
      text: errorMessage.delete_smalll_text,
      icon: errorMessage.delete_dialogue_type,
      showCancelButton: true,
      confirmButtonText: errorMessage.delete_confirm_button,
      cancelButtonText: errorMessage.delete_cancel_button,
    }).then((result) => {
      if (result.value) {
        this.productService.deleteProductImage(object.id).subscribe(
          (response) => {
            if (response.success) {
              this.getProductImages();
            }
          },
        );
      }
    }).catch(swal.noop);
  }

}
